            <rd-point-tags v-if="editField === 'tags'" :on-change="onChangeTag" :items="tags"></rd-point-tags>
            <v-select v-if="editField === 'history_type'" v-model="editAll[editField]" :items="['COV', 'INTERVAL', 'COV_AND_INTERVAL']" :label="toUpperTextKey(editField)" ></v-select>
            <v-checkbox v-if="typeof editAll[editField] === 'boolean'" v-model="editAll[editField]" :label="toUpperTextKey(editField)"/>
            <v-text-field v-if="typeof editAll[editField] === 'number'" v-model.number="editAll[editField]" min="0" :label="toUpperTextKey(editField)" type="number" @input="numberChange()"></v-text-field>
            <v-text-field v-if="typeof editAll[editField] === 'string' && editField !== 'history_type' && editField !== 'tags'" v-model="editAll[editField]" :label="toUpperTextKey(editField)" ></v-text-field>
          </v-card-text>
